$stopwatch-size: 400px;
$stopwatch-center-size: $stopwatch-size * 0.04;
$stopwatch-minuts-size: $stopwatch-size * 0.86;
$stopwatch-seconds-size: $stopwatch-size * 0.98;
$stopwatch-milliseconds-size: $stopwatch-size * 0.3;
$minuts-hand-size: $stopwatch-minuts-size / 2;
$seconds-hand-size: $stopwatch-seconds-size / 2;
$milliseconds-hand-size: $stopwatch-milliseconds-size / 2;